.navigation {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 8;
  padding: 16px 40px 16px 40px;
  border: 1px solid #316ced;
  border-radius: 8px;
  backdrop-filter: blur(10px);
  background: rgba(27, 60, 132, 0.2);
}
@media (max-width: 768px) {
  .navigation {
    padding: 16px;
  }
}
.navigation li {
  margin-right: 10px;
  color: #efefef;
  font-size: 16px;
  font-weight: 500;
  line-height: 120%;
  letter-spacing: 0%;
  text-align: left;
}
.navigation li a {
  position: relative;
  color: #efefef;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
}
.navigation li a span {
  margin: 0px 8px;
}
.navigation li a .navigation-mark {
  position: absolute;
  left: -16px;
  opacity: 0;
}
.navigation li a:focus .navigation-mark {
  opacity: 1;
  width: 16px;
  height: 16px;
  border-radius: 4px;
  background-color: #efefef;
  top: 50%;
  transform: translateY(-50%);
}

.tablet-navigation {
  z-index: 99999;
  position: sticky;
  top: 24px;
}
.tablet-navigation .header-menu-icon {
  cursor: pointer;
}
.tablet-navigation .navigation {
  position: absolute;
  flex-direction: column;
  width: 100%;
  transition: margin-top 0.2s linear;
  max-height: 56px;
}
.tablet-navigation .navigation.active {
  max-height: 334px;
}
.tablet-navigation .visible-tablet-navigation {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.tablet-navigation .navigation.active > * + * {
  margin-top: 10px;
}
.tablet-navigation .trigger {
  cursor: pointer;
}
.tablet-navigation .menu-dropdown {
  max-height: 0px;
  overflow: hidden;
  width: 100%;
  transition: max-height 0.2s linear, margin-top 0.2s linear;
}
.tablet-navigation .menu-dropdown ul {
  margin-top: 54px;
}
.tablet-navigation .menu-dropdown li {
  margin: 24px 0;
}
.tablet-navigation .menu-dropdown li a:focus {
  color: #6faaff;
}
.tablet-navigation .navigation.active .menu-dropdown {
  max-height: 318px;
}

